<template>
    <section class="search">
     <h1>Apex</h1>
     <form v-on:submit.prevent="onSubmit">
         <div class="form-group">
             <label for="platform">Platform</label>
             <select name="platform" id="platform" v-model="platform">
                 <option value="psn">Playstation</option>
                 <option value="xbl">Xbox</option>
                 <option value="battlenet">Battlenet</option>
             </select>
         </div>
         <div class="form-group">
             <label for="gamertag">Gamertag</label>
             <input type="text" name="text" v-model="gamertag" id="gamertag" placeholder="Battlenet ID, Xbox gamertag, PSN ID" />
         </div>
         <div class="form-group">
             <input type="submit" value="Submit" class="btn" />
         </div>
     </form>
    </section>
</template>






<script>
export default {
    name: 'Test',
    data(){
        return{
          platform: "psn",
          gamertag: "",

        }
    },
    // hook to show background img
    beforeCreate() {
        document.body.className = "body-bg-image";
    },
    methods: {
        onSubmit(){
            if(!this.gamertag){
               this.$toasted.show("Please enter a gamertag", {
                   duration: 3000,
                   icon: "exclamation-circle",
                   theme: "bubble", 
               });

               
            }else{
                this.$router.push(`/profile/${this.platform}/${this.gamertag}/apex`);
            }
        }
    },
}
</script>



